/*
 * HomePage Messages
 *
 * This contains all the text for the HomePage component.
 */
import { defineMessages } from 'react-intl';

export const scope = 'About';

export default defineMessages({
  title: {
    id: `${scope}.title`,
    defaultMessage: '',
  },
  description: {
    id: `${scope}.description`,
    defaultMessage: '',
  },
  contact: {
    id: `${scope}.contact`,
    defaultMessage: '',
  },
  share: {
    id: `${scope}.share`,
    defaultMessage: '',
  },
  introduce: {
    id: `${scope}.introduce`,
    defaultMessage: '',
  },
});
